import React, {useState} from 'react';
import styles from './choice_quiz.css';

function AnswersDiv(props) {
    const [value,setValue] = useState("");

    const handleChange = e => {
      if(props.view === "studentView") {
      console.log("SEL Value is"+e.target.value);
      setValue(e.target.value);
      props.setSelectedAnswer(e.target.name,props.questId);
      }
    }

    var classDisplay = styles.customButton;
    console.log("from Answer page"+props.view);
    console.log("Ans content & Ans correct " + props.answerContent +"   "+ props.correct_answer )
    if (props.view === "teacherView" && props.answerContent === props.correct_answer) {
      classDisplay = [styles.customButton, styles.correctans].join(" ")
    }
    if (props.view === "gradeView" || props.view === "feedbackView") {
      if ((props.selected_answer === props.correct_answer && props.answerContent === props.selected_answer) || props.answerContent === props.correct_answer)
        classDisplay = [styles.customButton, styles.correctans].join(" ");
      else if (props.selected_answer === props.answerContent)
      classDisplay = [styles.customButton, styles.wrongans].join(" ");
     }
  
  console.log("classDisplay IS:"+classDisplay);    

    return (
        <div className="col-md-6 col-sm-12 col-xs-12">
            {/* <img src={props.answerContent} /> */}
            <input
        type="image"
        className={classDisplay}
        name={props.answerContent}
        src={props.answerContent}
        disabled={props.selected_answer === props.answerContent? true : false}
        onClick={handleChange}
            />
        </div>

    );
}

export default AnswersDiv;