import React, { Component } from 'react'
import Question from './Question';
import Answers from './Answers'
import AnswersDiv from './AnswersDiv';
import Result from './result';
import styles from './choice_quiz.css'

class Choice extends Component {

    constructor(props) {
        super(props);
        this.renderAnswerOptions = this.renderAnswerOptions.bind(this);
        this.renderAnswerOptionsDiv = this.renderAnswerOptionsDiv.bind(this);
        this.state = {
            quiz: {
                questions: [
                    {
                        Id: "",
                        selected_answer: null
                    },
                    {
                        Id: "",
                        selected_answer: null
                    },
                    {
                        Id: "",
                        selected_answer: null
                    },
                    {
                        Id: "",
                        selected_answer: null
                    }]
            },
        }
    }

    setAnswerDetails = (ans, qid) => {
        console.log("Frm Answer Details paage" + ans+"    :"+qid);
        this.setState(this.state.quiz.questions.map(ques => {
        console.log("FFFFF"+ques.Id);
            if( ques.Id === qid) {
                console.log("From if loop")
                ques.selected_answer = ans
            }
            else
                console.log("Frm else oooop")
            
    }));
         this.state.quiz.questions.map(temp => console.log("SSS"+temp.selected_answer))
    }

    renderAnswerOptions(key,questId,selAns,corrAns) {

        console.log("this.propssssss" + this.props.onAnswerSelected+"view:"+this.props.view);
        console.log("Frm render options "+questId+"selAns is "+selAns+ "Correct Ans :"+corrAns);
        return (

            <div className="col-md-6 col-sm-12 col-xs-12">
                <Answers
                    answerContent={key.content}
                    onAnswerSelected={this.props.onAnswerSelected}
                    disable={this.props.disable}
                    setSelectedAnswer={this.setAnswerDetails}
                    questId = {questId}
                    view={this.props.view}
                    selected_answer={selAns}
                    correct_answer = {corrAns}
                />
            </div>
        );
    }

    renderAnswerOptionsDiv(key,questId,selAns,corrAns) {
        console.log("inside div answer", key.content)

        return (
            <div className="col-md-6 col-sm-12 col-xs-12">
                <AnswersDiv
                    answerContent={key.content}
                    onAnswerSelected={this.props.onAnswerSelected}
                    disable={this.props.disable}
                    setSelectedAnswer={this.setAnswerDetails}
                    questId = {questId}
                    view={this.props.view}
                    selected_answer={selAns}
                    correct_answer = {corrAns}
                />
            </div>
        );
    }

    render() {
        var selAns = "";
        return (
            <div>
                {this.props.question.map((quest, index) => {
                    {this.state.quiz.questions[index].Id=quest.Id
                     if(this.props.view === "studentView")   
                        selAns = this.state.quiz.questions[index].selected_answer;
                     else
                        selAns = quest.selected_answer;
                    }
                    return(
                    <div className={[styles.card,styles.lessoncard].join(" ")}>
                        <div className={styles.cardHeader}>
                            <h5>Question <span>{index + 1}</span></h5>
                        </div>
                        <div className={styles.cardBlock}>
                            <div className={styles.popuptxt} key={index}></div>
                            <Question content={quest.question} questId={quest.Id} imagepath={quest.questionImagePath} qtype={quest.questionType} />
                            

                            <div className="row">
                                {(quest.AnswerType === "text") ? quest.answers.map((key => this.renderAnswerOptions(key,quest.Id,selAns, quest.correctAns))) : quest.answers.map((key => this.renderAnswerOptionsDiv(key,quest.Id,selAns,quest.correctAns)))}
                            </div>
                        </div>
                    </div>
                    )
                })}

              {  (this.props.view === "studentView")?<Result  {...this.state} /> : "" }
            </div>
        );
    }
}
export default Choice;
