import React, { Component } from 'react';
import Choice from './Choice';


 class MultiChoice extends Component {

  constructor(props) {
    super(props);
  
    this.state = {     
      correctAns: '',
      disable: false
    };
    this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
  }
   

  handleAnswerSelected(event) {
    var stuSelection = [
                { "QuesId": "Q1", "selected_answer": null },
                { "QuesId": "Q2", "selected_answer": null },
                { "QuesId": "Q3", "selected_answer": "A.Mechanical" },
                { "QuesId": "Q4", "selected_answer": "C.Mechanical" }
             ]
    const correctAnsList = this.props.data.map((question) => {
      stuSelection.map(students => {
        if(question.Id === students.QuesId) {
          if(question.correctAns === students.selected_answer){
              question.grade_points = 1;
          } else
              question.grade_points = 0;
        }

      }) 

    });
  
  }

  render() {

    return (
      
      <div>
         
      <Choice
        question={this.props.data}
        disable={this.state.disable}
        onAnswerSelected={this.handleAnswerSelected}
        view={this.props.view}
      />
    
      </div>
    )
  }
}
export default MultiChoice ;
